import React, {Component} from 'react';
import MalexLabel from 'malextrap-react/lib/Label';

export default class Post extends Component {


    /** SUPER **/

    render(){
        const data = this.props.data;
        if (data === undefined) return;
        return (
            <div className={"post"}>
                <img alt={"avatar"} className={"avatar"} src={"https://avatars3.githubusercontent.com/u/20615143?v=3&s=460"} />
                <div className={"content"}>
                    <a target={"_blank"} href={data.url} className={"header no-decoration"}>
                        {data.name} <br /><MalexLabel color={"pink"} text={"REPO"} />
                    </a>
                    <div className={"description"}>
                        {data.description}
                    </div>
                </div>
            </div>
        )
    }
}