import React, {Component} from 'react';
import LabSVG from './svg/labsvg';
import Experiment from './experiment';
import config from '../../config/config.json';


export default class Experiments extends Component {


    /** SUPER **/

    render(){
        return (
            <div className={"experiments container"}>
                <div className={"experiments-title"}>
                    <h3 className={"exp-title"}><LabSVG width={32} height={32} /> Last experiments</h3>
                    <p className={"exp-subtitle"}>Quality is more important than quantity. â€” S. Jobs</p>
                </div>,
                <div className={"columns"}>
                    {this.renderExperiments()}
                </div>
            </div>
        )
    }


    /** REST **/

    /**
     * This function render all experiments from config
     * @return {Experiment} experiment
     */
    renderExperiments(){
        return config.experiments.map(e => 
            <Experiment 
                name={e.name}
                date={e.date}
                href={e.href}
                image={e.image}
                backgroundColor={e.backgroundColor}
                fontColor={e.fontColor}
            />
        )
    }
}