import React, {Component} from 'react';


export default class Experiment extends Component {


    /** SUPER **/

    render(){
        const css = {
            image: {
                backgroundImage: `url('${this.props.image}')`
            },
            color: {
                backgroundColor: this.props.backgroundColor,
                color: this.props.fontColor
            }
        };

        return (
            <div className={"experiment col-md-6 col-lg-4"}>
                <a className={"experiment-img"} href={this.props.href} style={css.image} />
                <div className="experiment-info" style={css.color}>
                    <a className={"no-decoration"} href={this.props.href}>{this.props.name}</a>
                    <time className={"experiment-date"} dateTime={this.props.date}>{this.props.date}</time>
                </div>
            </div>
        )
    }
}